"use client";

import { useEffect, useMemo, useState } from "react";

type PlayerGameBaselineRow = {
  player_id: string;
  player_name: string;
  game_date: string;
  park_id: string;
  opposing_pitcher_id: string;
  hr_rate_last_50: number;
  season_hr_rate: number;
  park_hr_factor: number;
  expected_pa: number;
  baseline_hr_score: number;
};

const BASELINE_CSV_PATH = "/data/player_game_baseline.csv";

function parseNumber(value: string): number {
  const n = Number(value);
  return Number.isFinite(n) ? n : 0;
}

function splitCsvLine(line: string): string[] {
  const out: string[] = [];
  let cur = "";
  let inQuotes = false;

  for (let i = 0; i < line.length; i += 1) {
    const ch = line[i];

    if (ch === '"') {
      if (inQuotes && line[i + 1] === '"') {
        cur += '"';
        i += 1;
        continue;
      }
      inQuotes = !inQuotes;
      continue;
    }

    if (ch === "," && !inQuotes) {
      out.push(cur);
      cur = "";
      continue;
    }

    cur += ch;
  }

  out.push(cur);
  return out;
}

function parseBaselineCsv(csvText: string): PlayerGameBaselineRow[] {
  const text = csvText.replace(/^\uFEFF/, "").trim();
  if (!text) return [];

  const lines = text.split(/\r?\n/).filter(Boolean);
  if (lines.length < 2) return [];

  const header = splitCsvLine(lines[0]).map((h) => h.trim());
  const idx = new Map(header.map((h, i) => [h, i]));

  function getCell(cells: string[], key: string): string {
    const i = idx.get(key);
    return i === undefined ? "" : (cells[i] ?? "").trim();
  }

  const rows: PlayerGameBaselineRow[] = [];
  for (const line of lines.slice(1)) {
    const cells = splitCsvLine(line);

    const player_id = getCell(cells, "player_id");
    const game_date = getCell(cells, "game_date");
    if (!player_id || !game_date) continue;

    const player_name =
      getCell(cells, "player_name") ||
      getCell(cells, "full_name") ||
      player_id;

    rows.push({
      player_id,
      player_name,
      game_date,
      park_id: getCell(cells, "park_id"),
      opposing_pitcher_id: getCell(cells, "opposing_pitcher_id"),
      hr_rate_last_50: parseNumber(getCell(cells, "hr_rate_last_50")),
      season_hr_rate: parseNumber(getCell(cells, "season_hr_rate")),
      park_hr_factor: parseNumber(getCell(cells, "park_hr_factor")),
      expected_pa: parseNumber(getCell(cells, "expected_pa")),
      baseline_hr_score: parseNumber(getCell(cells, "baseline_hr_score")),
    });
  }

  return rows;
}

export default function BaselinePage() {
  const [rows, setRows] = useState<PlayerGameBaselineRow[] | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [selectedDate, setSelectedDate] = useState<string>("");

  useEffect(() => {
    let cancelled = false;

    async function load() {
      try {
        setError(null);
        const res = await fetch(BASELINE_CSV_PATH, { cache: "no-store" });
        if (!res.ok) {
          throw new Error(
            `Missing baseline file at ${BASELINE_CSV_PATH} (HTTP ${res.status}).`,
          );
        }
        const text = await res.text();
        const parsed = parseBaselineCsv(text);
        if (cancelled) return;
        setRows(parsed);

        const dates = Array.from(new Set(parsed.map((r) => r.game_date))).sort();
        setSelectedDate((prev) => prev || dates[dates.length - 1] || "");
      } catch (e) {
        if (cancelled) return;
        setRows([]);
        setError(e instanceof Error ? e.message : String(e));
      }
    }

    load();
    return () => {
      cancelled = true;
    };
  }, []);

  const dates = useMemo(() => {
    if (!rows) return [];
    return Array.from(new Set(rows.map((r) => r.game_date))).sort();
  }, [rows]);

  const ranked = useMemo(() => {
    if (!rows) return [];
    const filtered = selectedDate
      ? rows.filter((r) => r.game_date === selectedDate)
      : rows;
    return filtered
      .slice()
      .sort((a, b) => {
        if (b.baseline_hr_score !== a.baseline_hr_score)
          return b.baseline_hr_score - a.baseline_hr_score;
        if (b.hr_rate_last_50 !== a.hr_rate_last_50)
          return b.hr_rate_last_50 - a.hr_rate_last_50;
        return a.player_id.localeCompare(b.player_id);
      })
      .slice(0, 25);
  }, [rows, selectedDate]);

  return (
    <div className="flex flex-col gap-4">
      <div className="flex flex-col gap-1">
        <h1 className="text-2xl font-bold tracking-tight">HR Picks (Baseline)</h1>
        <p className="text-sm text-slate-600 dark:text-slate-400">
          Source: <span className="font-mono">{BASELINE_CSV_PATH}</span>
        </p>
      </div>

      {error ? (
        <div className="rounded-lg border border-amber-200 bg-amber-50 p-4 text-sm text-amber-900 dark:border-amber-900/40 dark:bg-amber-950/30 dark:text-amber-200">
          <p className="font-semibold">Baseline file not found</p>
          <p className="mt-1">{error}</p>
          <p className="mt-3 font-mono text-xs whitespace-pre-wrap">
            {`mkdir -p public/data
cp scripts/ml/data/player_game_baseline.csv public/data/player_game_baseline.csv`}
          </p>
          <p className="mt-2">
            Then refresh this page:{" "}
            <span className="font-mono">/baseline</span>
          </p>
        </div>
      ) : null}

      <div className="flex flex-col gap-3 rounded-lg border border-slate-200 bg-white p-4 dark:border-slate-800 dark:bg-slate-950">
        <div className="flex flex-col gap-2 sm:flex-row sm:items-end sm:justify-between">
          <div className="flex flex-col gap-1">
            <label className="text-xs font-semibold uppercase tracking-wide text-slate-500 dark:text-slate-400">
              Date
            </label>
            <select
              className="w-full rounded-md border border-slate-200 bg-white px-3 py-2 text-sm shadow-sm dark:border-slate-800 dark:bg-slate-950"
              value={selectedDate}
              onChange={(e) => setSelectedDate(e.target.value)}
              disabled={!dates.length}
            >
              {dates.length ? null : <option value="">No dates loaded</option>}
              {dates.map((d) => (
                <option key={d} value={d}>
                  {d}
                </option>
              ))}
            </select>
          </div>

          <div className="text-sm text-slate-600 dark:text-slate-400">
            Showing <span className="font-semibold">{ranked.length}</span>{" "}
            players
          </div>
        </div>

        <div className="overflow-x-auto">
          <table className="w-full text-left text-sm">
            <thead className="border-b border-slate-200 text-xs uppercase tracking-wide text-slate-500 dark:border-slate-800 dark:text-slate-400">
              <tr>
                <th className="py-2 pr-4">#</th>
                <th className="py-2 pr-4">Player</th>
                <th className="py-2 pr-4">HR Score</th>
                <th className="py-2 pr-4">Last 50</th>
                <th className="py-2 pr-4">Season</th>
                <th className="py-2 pr-4">Park</th>
                <th className="py-2 pr-4">Exp PA</th>
              </tr>
            </thead>
            <tbody>
              {rows === null ? (
                <tr>
                  <td className="py-4 text-slate-500 dark:text-slate-400" colSpan={7}>
                    Loading…
                  </td>
                </tr>
              ) : ranked.length ? (
                ranked.map((r, i) => (
                  <tr
                    key={`${r.player_id}:${r.game_date}`}
                    className="border-b border-slate-100 last:border-b-0 dark:border-slate-900"
                  >
                    <td className="py-2 pr-4 tabular-nums text-slate-500 dark:text-slate-400">
                      {i + 1}
                    </td>
                    <td className="py-2 pr-4">
                      <div className="font-medium">{r.player_name}</div>
                      {r.player_name !== r.player_id ? (
                        <div className="mt-0.5 text-xs font-mono text-slate-500 dark:text-slate-400">
                          {r.player_id}
                        </div>
                      ) : null}
                    </td>
                    <td className="py-2 pr-4 tabular-nums font-semibold">
                      {r.baseline_hr_score.toFixed(3)}
                    </td>
                    <td className="py-2 pr-4 tabular-nums">
                      {(r.hr_rate_last_50 * 100).toFixed(1)}%
                    </td>
                    <td className="py-2 pr-4 tabular-nums">
                      {(r.season_hr_rate * 100).toFixed(1)}%
                    </td>
                    <td className="py-2 pr-4 tabular-nums">
                      {r.park_hr_factor ? r.park_hr_factor.toFixed(2) : "—"}
                    </td>
                    <td className="py-2 pr-4 tabular-nums">
                      {r.expected_pa ? r.expected_pa.toFixed(1) : "—"}
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td className="py-4 text-slate-500 dark:text-slate-400" colSpan={7}>
                    No rows for this date.
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}
